 .nav {
   &__list {
     margin: 0;
     padding: 0;
     list-style: none;
     display: flex;
     flex-direction: column;
     gap: 32px;
    }

    &__link {
    position: relative;
    color: inherit;
    text-decoration: none; 
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px; 

    &::after {
    position: absolute;
    content: '';
    color: $main-text-color;
    bottom: -8px;
    display: block;
    width: 100%;
    height: 1px;

    transition: transform 0.3s;
    transform: scale(0);
    }

    &:hover::after {
      transform: scale(1);
    }
    }
  }
